import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LandPageComponent } from './landPage/landingPage.component'
import { PersonalDetailComponent } from './landPage/personalDetails/personalDetail.component';
import { SummmarySkillComponent } from './landPage/summarySkill/summarySkill.component'; 
import { EducationSectionComponent } from './landPage/educationSection/educationSection.component';


const routes: Routes = [

  {
    path:'',
    component:LandPageComponent,
    children: [
      
         { path: '',
          component:PersonalDetailComponent},

          {path: 'summary-skill',
          component:SummmarySkillComponent},

          {path: 'education-section',
          component:EducationSectionComponent }

        ]
  },
 
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
